import { Avatar } from "@chakra-ui/avatar";
import { useHistory } from "react-router-dom";
import { useState } from "react";
import axios from "axios";
import { useToast } from "@chakra-ui/toast";
import ChatLoading from "../ChatLoading";
import { Spinner } from "@chakra-ui/spinner";
import ProfileModal from "./ProfileModal";
import NotificationBadge from "react-notification-badge";
import { Effect } from "react-notification-badge";
import { getSender } from "../../config/ChatLogics";
import UserListItem from "../userAvatar/UserListItem";
import { ChatState } from "../../Context/ChatProvider";
import FetchNoti from "../../contracts/frontend-interaction/FetchNoti";

function SideDrawer() {
  const [search, setSearch] = useState("");
  const [searchResult, setSearchResult] = useState([]);
  const [loading, setLoading] = useState(false);
  const [loadingChat, setLoadingChat] = useState(false);

  const {
    setSelectedChat,
    user,
    notification,
    setNotification,
    chats,
    setChats,
  } = ChatState();

  const toast = useToast();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const history = useHistory();

  const logoutHandler = () => {
    localStorage.removeItem("userInfo");
    localStorage.removeItem("address");
    history.push("/");
  };

  const handleSearch = async () => {
    if (!search) {
      toast({
        title: "Please Enter something in search",
        status: "warning",
        duration: 5000,
        isClosable: true,
        position: "top-left",
      });
      return;
    }

    try {
      setLoading(true);

      const config = {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      };

      const { data } = await axios.get(`https://sake-backend.onrender.com/api/user?search=${search}`, config);

      setLoading(false);
      setSearchResult(data);
    } catch (error) {
      toast({
        title: "Error Occured!",
        description: "Failed to Load the Search Results",
        status: "error",
        duration: 5000,
        isClosable: true,
        position: "bottom-left",
      });
    }
  };

  const accessChat = async (userId) => {
    console.log(userId);

    try {
      setLoadingChat(true);
      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${user.token}`,
        },
      };
      const { data } = await axios.post(`https://sake-backend.onrender.com/api/chat`, { userId }, config);

      if (!chats.find((c) => c._id === data._id)) setChats([data, ...chats]);
      setSelectedChat(data);
      setLoadingChat(false);
      onClose();
    } catch (error) {
      toast({
        title: "Error fetching the chat",
        description: error.message,
        status: "error",
        duration: 5000,
        isClosable: true,
        position: "bottom-left",
      });
    }
  };

  const [notification2, setNotification2] = useState([]);

  return (
    <>
      <Box
        d="flex"
        justifyContent="space-between"
        alignItems="center"
        bg="rgb(30,33,36)"
        w="100%"
        p="5px 10px 5px 10px"
        color="white"
        className="font1"
      >
        {/* <Tooltip label="Search Users to chat" hasArrow placement="bottom-end">
          <Button
            variant="ghost"
            onClick={onOpen}
            _hover={{ bg: "rgb(3, 252, 173);", color: "black" }}
          >
            <i className="fas fa-search"></i>
            <Text d={{ base: "none", md: "flex" }} px={4}>
              Search User
            </Text>
          </Button>
        </Tooltip> */}
        <img
          src={img1}
          style={{
            width: "100px",
            height: "100px",
            objectFit: "cover",
            position: "absolute",
            top: "-18px",
            left: "0px",
          }}
        ></img>
        <Text fontSize="2xl" fontFamily="Work sans">
          {/* Talk-A-Tive */}
        </Text>
        <div>
          <Menu>
            <MenuButton
              p={1}
              onClick={async() => {
                const result = await FetchNoti(localStorage.getItem("address"));
                const temp = result.feeds.map((d) => {
                  return (
                    <Noti
                      sender={d.sender}
                      body={d.payload.notification.body}
                      title={d.payload.notification.title}
                    ></Noti>
                  );
                });
                setNotification2(temp);
              }}
            >
              <NotificationBadge
                count={notification.length}
                effect={Effect.SCALE}
              />
              <BellIcon fontSize="2xl" m={1} />
            </MenuButton>
            <MenuList
              pl={2}
              zIndex={"10001"}
              _hover={{ bg: "rgb(3, 252, 173);", color: "black" }}
              bg="rgb(30,33,36)"
              border={"0px"}
              color={"white"}
            >
              {!notification2.length && "No New Messages"}
              {notification2}
            </MenuList>
          </Menu>
          {/* <Menu>
            <MenuButton p={1}>
              <NotificationBadge
                count={notification.length}
                effect={Effect.SCALE}
              />
              <BellIcon fontSize="2xl" m={1} />
            </MenuButton>
            <MenuList
              pl={2}
              zIndex={"10001"}
              _hover={{ bg: "rgb(3, 252, 173);", color: "black" }}
              bg="rgb(30,33,36)"
              border={"0px"}
              color={"white"}
            >
              {!notification.length && "No New Messages"}
              {notification.map((notif) => (
                <MenuItem
                  key={notif._id}
                  onClick={() => {
                    setSelectedChat(notif.chat);
                    setNotification(notification.filter((n) => n !== notif));
                  }}
                >
                  {notif.chat.isGroupChat
                    ? `New Message in ${notif.chat.chatName}`
                    : `New Message from ${getSender(user, notif.chat.users)}`}
                </MenuItem>
              ))}
            </MenuList>
          </Menu> */}
          <Menu>
            <MenuButton
              as={Button}
              rightIcon={<ChevronDownIcon />}
              _hover={{ bg: "rgb(3, 252, 173);", color: "black" }}
              bg="rgb(30,33,36)"
              border={"0px"}
            >
              <FaUser />
            </MenuButton>
            <MenuList bg="rgb(66,69,73)" border={"0px"} zIndex={"10001"}>
              <ProfileModal user={user}>
                <MenuItem
                  _hover={{ bg: "rgb(3, 252, 173);", color: "black" }}
                  _focus={{ bg: "rgb(3, 252, 173);", color: "black" }}
                  border={"0px"}
                >
                  My Profile
                </MenuItem>{" "}
              </ProfileModal>
              <MenuDivider />
              <MenuItem
                onClick={logoutHandler}
                _hover={{ bg: "rgb(3, 252, 173);", color: "black" }}
                _focus={{ bg: "rgb(3, 252, 173);", color: "black" }}
                border={"0px"}
              >
                Logout
              </MenuItem>
            </MenuList>
          </Menu>
        </div>
      </Box>

      {/* <Drawer placement="left" onClose={onClose} isOpen={isOpen}>
        <DrawerOverlay />
        <DrawerContent>
          <DrawerHeader borderBottomWidth="1px">Search Users</DrawerHeader>
          <DrawerBody>
            <Box d="flex" pb={2}>
              <Input
                placeholder="Search by name or email"
                mr={2}
                value={search}
                onChange={(e) => setSearch(e.target.value)}
              />
              <Button onClick={handleSearch}>Go</Button>
            </Box>
            {loading ? (
              <ChatLoading />
            ) : (
              searchResult?.map((user) => (
                <UserListItem
                  key={user._id}
                  user={user}
                  handleFunction={() => accessChat(user._id)}
                />
              ))
            )}
            {loadingChat && <Spinner ml="auto" d="flex" />}
          </DrawerBody>
        </DrawerContent>
      </Drawer> */}
    </>
  );
}

export default SideDrawer;
